:root{
    --color-primary: #0091ea
}
*{
    padding: 0;
    margin: 0;
}
.tool{
transform: scale(2);
transition: transform 0.5s;
transition-delay: 0.1s;
}
.tool-glide:hover{
    transform: scale(2.5);
    color: #80d8ff;
}
body{
    cursor: url("../src/assets/eraser.svg");
}
.tool-selected{
    transform: scale(2);
    color: var(--color-primary);

}
.container{
    background-color: #d60a0a;
    height: 5px;
    width: 100%;
    margin: 0 auto;

}
.bar-container{
    background-color: #f0f0f0;
    height: 5px;
    width: 100%;
    /* margin: 0 auto; */
    border-radius: 5px;
}
.bar{
    background-color: var(--color-primary);
    height:5px;
    width: 40%;
    border-radius: 5px;
    transition: width 400ms;
}
.more{
    display: flex;
    flex-direction: row;
    overflow-y: auto;
    position: absolute;
    background: red;
    z-index:10;
    top: 100%;
    left:0;
}
.drop-down-icon{
    border: none;
    margin: 0 auto;
}
.drop-down-marker::after{
    content: "â–¼";
    width: 10px;height: 10px;
    margin: 0 auto;
    position: relative;

}
.drop-down-wrapper{
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
